/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

dtpc-progress-bar {
	--dtpc-slider--scrubber--color: var(
		--dtpc-progress-bar--scrubber--color,
		var(--_dtpc-player--foreground, currentColor)
	);
	--dtpc-slider--scrubber--size: var(
		--dtpc-progress-bar--scrubber--size,
		1.25rem
	);
	--dtpc-slider--scrubber--border-color: var(
		--dtpc-progress-bar--scrubber--border-color,
		var(--_dtpc-player--foreground, currentColor)
	);
	--dtpc-slider--scrubber--border-width: var(
		--dtpc-progress-bar--scrubber--border-width,
		0.125rem
	);
	--dtpc-slider--scrubber--border-radius: var(
		--dtpc-progress-bar--scrubber--border-radius,
		100vw
	);
	--dtpc-slider--progress--color: var(
		--dtpc-progress-bar--progress--color,
		var(--_dtpc-player--foreground, currentColor)
	);
	--dtpc-slider--track--color: var(
		--dtpc-progress-bar--track--color,
		transparent
	);
	--dtpc-slider--track--height: var(--dtpc-progress-bar--track--height, 0.5rem);
	--dtpc-slider--track--radius: var(--dtpc-progress-bar--track--radius, 100vw);
	--dtpc-slider--track--border-color: var(
		--dtpc-progress-bar--track--border-color,
		var(--_dtpc-player--foreground, currentColor)
	);
	--dtpc-slider--track--border-width: var(
		--dtpc-progress-bar--track--border-width,
		0.125rem
	);
	--dtpc-slider--track--border-offset: var(
		--dtpc-progress-bar--track--border-offset,
		0.125rem
	);
}
